import React, { Component } from 'react';
import './App.css';
import YouTube from 'react-youtube';

class App extends Component {

  constructor(props) {
    super(props);
    this.state = {
      videoList: [],
      after: ''
    }
  }

  componentDidMount() {
    this.callRedditApi();
  }

  callRedditApi(after = '') {
    fetch(`https://www.reddit.com/r/nba.json?after=${after}`).then(resp => resp.json()).then(data => {
      let videos = data.data.children.filter(item => {
        let url = item.data.url;
        return url.match(/streamable/) || url.match(/youtube.com\/watch/) || url.match(/youtu.be/)
      })
      let preparedVideos = videos.map((v, index) => {
        return this.prepareVideos(v, index);
      })
      this.setState({
        after: data.data.after,
        videoList: this.state.videoList.concat(preparedVideos),
      })
      if(this.state.videoList.length < 10) {
        this.callRedditApi(this.state.after);
      }
    })
  }

  prepareVideos(video, index){
    let currentVid = {};
    let sourceUrl = video.data.url;
    let url = '';
    let thumbnail = '';
    if(sourceUrl.match(/streamable/)) {
      url = `//cdn.streamable.com/video/mp4/${sourceUrl.split('/')[3]}.mp4`
      thumbnail = `//cdn.streamable.com/image/${sourceUrl.split('/')[3]}.jpg`
      currentVid.isStreamable = true;
    } else if (sourceUrl.match(/youtube.com\/watch/)){
      url = sourceUrl.split('=')[1].substring(0,11);
      currentVid.isStreamable = false;
    } else if(sourceUrl.match(/youtu.be/)) {
      url = sourceUrl.split('/')[3];
      currentVid.isStreamable = false;
    }
    currentVid.url = url;
    currentVid.thumbnail = thumbnail;
    currentVid.index = index;
    currentVid.title = video.data.title;
    return currentVid;
  }
  
  render() {
    if (!this.state.videoList.length) {
      return <div> Loading... </div>
    }
    function attachCustomAttributes(domNode) {
      if (domNode) {
        domNode.setAttribute('webkit-playsinline', '');
      }
    }
    return (
      <div className="App">
        <div className="App-header">
        {this.state.videoList.map((video, index) => {  
          return <div key={index} id={index} style={{paddingBottom: 60, minHeight: 300}}>
            <p className="title">{video.title}</p>
            {video.isStreamable ?
            <video src={video.url} controls className="video" ref={attachCustomAttributes} poster={video.thumbnail}>
              Your browser does not support the <code>video</code> element.
            </video>
            :
            <YouTube
              videoId={video.url}
              className="video"
            />
            }
          </div>
        })}
        </div>
      </div>
    );
  }
}

export default App;



